import { useEffect, useState } from 'react';
import {useParams} from "react-router-dom";
import ReactPlayer from 'react-player';
import styles from '../css/Detail.module.css'

function Detail(){
  const {movieNo} = useParams();
  const [movie, setMovie] = useState({});
  const getMovie = async() => {
    const json = await (
      await fetch(`https://yts.mx/api/v2/movie_details.json?movie_id=${movieNo}`)
    ).json();
    setMovie(json.data.movie);
  };
  console.log(movie);
  useEffect(() => {
    getMovie();
  }, []);

  return (
    <div className={styles.body}>
      <div className={styles.container}>
        <div className={styles.contents}>
          <img src={movie.medium_cover_image} alt="coverImage" />
          <div>
            <h2>{movie.title}</h2>
            <div className={styles.content}>
              <p>rate: {movie.rating}</p>
              <p>running time: {movie.runtime}m</p>
              <p>{movie.description_intro}</p>
            </div>
          </div>
        </div>
  
        <div className={styles.mov}>
          <ReactPlayer 
            url={`https://www.youtube.com/watch?v=${movie.yt_trailer_code}`} 
            width="100%"
            height="100%"
          />
        </div>
      </div>
    </div>
  )
}

export default Detail;